.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px 30px;
    background: #ffffff;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    margin-bottom: 20px;
  }
  
  .header-content {
    display: flex;
    align-items: center;
  }
  
  .header-logo {
    width: 40px;
    height: 40px;
    margin-right: 15px;
  }
  
  header h1 {
    font-size: 24px;
    color: #333;
  }
  
  header button {
    background: #007BFF;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    font-size: 16px;
    color: white;
    transition: background 0.3s;
  }
  
  header button:hover {
    background: #0056b3;
  }
  